import React from "react"
import { DomsStyled } from "./Doms.styled"
import Dom from "./Dom"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
import { faCaretDown, faCaretUp } from "@fortawesome/pro-solid-svg-icons"
import { H5Styled } from "./Domains.styled"
import DomsSuggested from "./DomsSuggested"

let list_limit = function (list_length) {
  console.log('list_limit')
  // defaults for each category
  // Note: (one_list ? 6 : 9)
  // First "?" number is how many to show all at once on top
  // Second ":" number is how many to scatter in ever other few
  let limit = 3
  // show all available
  // limit = Math.max(limit, available)
  // adjust to screen size
  if (typeof window === "object") {
    /*
     * adjust to screen size
     */
    // 3 columns
    if (window.innerWidth > 1299) {
      limit = 6
      if (list_length<6) {
        limit = 3
      }
    }
    // 2 columns
    else if (window.innerWidth > 899) {
      limit = 4
      if (list_length<4) {
        limit = 2
      }
    }
    // 1 column
    else {
      if (limit === 9) {
        limit = 3
      }
    }
  }
  // done
  return limit
}

class Doms extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
    }
  }
  render() {
    /*
     * User "container" state/props/methods!
     * This "child" component is to simplify container's render() logic.
     */
    let that = this.props.that
    let { domains_generic, domains_availability, input_str, input_tld } = that.props

    /*
     * Validate
     */
    if (!domains_generic || !domains_generic.length) {
      return null
    }
    /*
     * Filter Unavailable/Aftermarket:
     */
    let Generic = [input_str+' .'+input_tld]
    if (domains_generic.length) {
      for (let phraseString of domains_generic) {
        let phraseStringNoSpaces = phraseString.replace(/ /g, "");
        if (phraseStringNoSpaces===input_str+'.'+input_tld) continue;
        let code = Number(domains_availability[phraseStringNoSpaces] || 0)
        if (
          (code === 9 || (code >= 10 && code <= 1000)) ||
          (code > 1000 && !!that.props.show_premium) ||
          (code < 9 && !!that.props.show_unavailable) ||
          (code === 0)
        ) {
          Generic.push(phraseString)
        }
      }
    }
    // limit more/less content:
    let GenericVisible = Generic.slice(0,this.state["unlimit_generic"] ? 1000 : list_limit(Generic.length))

    /*
     * more
     */
    let MoreLink = null
    if (Generic.length > GenericVisible.length) {
      MoreLink = (
        <div className="dom_name unlimit"
             onClick={() => {
               this.setState({
                 ["unlimit_generic"]: true
               })
             }}>
          <span>
            more &thinsp;
          </span>
            <FA className="fa-angle-down" icon={faCaretDown} />
        </div>
      )
    } else if (this.state["unlimit_generic"] === true) {
      MoreLink = (
        <div className="dom_name unlimit"
             onClick={() => {
               this.setState({
                 ["unlimit_generic"]: false
               })
             }}>
          <span>
            less &thinsp;
          </span>
            <FA className="fa-angle-up" icon={faCaretUp} />
        </div>
      )
    }

    /*
     * View
     */
    return (
      <DomsStyled className={"DomsStyled"}>

        {!!GenericVisible.length &&
        <div className={"doms_group"}>
          <H5Styled className="doms_title">
            Exact match + Generic TLDs
          </H5Styled>
          <div className="doms_content">
            {GenericVisible.map((phraseString, pi) => {
              if (phraseString[0] === "." || phraseString[1] === "." || phraseString[2] === ".") return null
              return <Dom that={that} key={phraseString + pi} phraseString={phraseString} />
            })}
            {MoreLink}
          </div>
        </div>
        }

        <DomsSuggested that={that} />

      </DomsStyled>
    )
  }
}

export default Doms
